

import ModalBase from "@/components/modal/ModalBase";
import VueNextSelect from "vue-next-select";
import "vue-next-select/dist/index.min.css";

export default {
  name: "ModalRegister",
  components: {
    ModalBase,
    VueNextSelect,
  },
  props: {
    getTranslateJsonField: Function,
    wordTranslation: Function,
  },
  data() {
    return {
      username: "",
      usernameError: "",
      password: "",
      passwordError: "",
      group: null,
      groups: [
        { title: "ОИБ-418", id: 1 },
        { title: "ОИБ-318", id: 2 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
        { title: "ОИБ-218", id: 3 },
      ],
    };
  },
  methods: {
    open() {
      return this.$refs.modalBase.open();
    },
    close() {
      return this.$refs.modalBase.close();
    },
    fioValidation() {
      if (!this.fio.length) {
        this.fioError = "Enter fio TRANSLATE SUKA";
        return false;
      }
      this.fioError = "";
      return true;
    },
    phoneValidation() {
      if (!this.phoneObject.valid) {
        this.phoneError = "asdsad TRANSLATE SUKA";
        return false;
      }
      this.phoneError = "";
      return true;
    },
    modalValidation() {
      this.fioValidation();
      this.phoneValidation();
      if (!this.phoneError.length && !this.fioError.length) {
        return this.$refs.modalBase.confirm({
          fio: this.fio, phone: this.phoneObject.formatted, text: this.comment,
          variants_execution_id: this.activeVariant,
        });
      }
    },
    async showError() {
      return new Promise(resolve => {
        this.$refs.modalBase.showError();
        setTimeout(() => {
          this.$refs.modalBase.hideError();
          resolve();
        }, 1000);
      });
    },
    async showSuccess() {
      return new Promise(resolve => {
        this.$refs.modalBase.showSuccess();
        setTimeout(() => {
          this.fio = "";
          this.phone = "";
          this.comment = "";
          this.$refs.modalWindowMask.clearPhone();
          this.$refs.modalBase.hideSuccess();
          this.$refs.modalBase.close();
          resolve();
        }, 1000);
      });
    },
  },
};
